.hero-img {
    background: rgba(0,0,0,0.3);
    height: 50vh;
    width: 100%;
    position: relative;
}
.hero-img:before {
    content: '';
    background: url('../assets/Earth.jpg') no-repeat center center/cover;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}
.hero-img .heading{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
}