.training{
    width: 1140px;
    height: 100%;
    margin: 3rem auto;
    display: grid;
    grid-template-columns: repeat(2,1fr);
}
.training .left{
    text-align: center;
    margin: 100px auto;
    padding: 1rem 0;
    max-width: 600px;
}
.training .left p {
    font-size: 1.2rem;
    margin: 1.2rem 0;
}
.training .right {
    width: 100%;
}
.training .right .img-container{
    display: grid;
    grid-template-columns: repeat(12 , 1fr);
    position: relative; 
    align-items: center;
    text-align: center;

}
.right .img{
    width:60%;
    height: 350px;
    border: 1px solid #333;
}
.right .top{
    grid-column: 1 / span 8;
    grid-row: 1;
    padding-top: 20%;
    z-index: 6;
}
.right .bottom{
    grid-column: 4 / -1;
    grid-row: 1;
    z-index: 5;
}

@media (max-width:1140px){
    .training {
        grid-template-columns: 1fr;
    }
    .training .img {
        max-width: 80%;
    }
}